.appbar {
    background-color: white;
    box-shadow: 0 1px 2px rgba(black, .1);

    &--branded {
        background: $color-primary-darker url('../images/header-bg.png') right top no-repeat;
        background-size: 90%;
        box-shadow: 0 1px 0 rgba(black, .2);
    }
}

.toolbar {
    display: flex;
    align-items: center;
    height: 64px;
    padding: 0 6px;

    @media all and (min-width: $size-md) {
        padding: 0 14px;
    }

    &__title {
        margin-left: 10px;
    }
}