.products {
    padding: 24px 0;
}

.product {
    display: block;
    padding: 24px;
    text-align: center;
    color: $color-text-primary;

    &__figure {
        display: inline-block;
        position: relative;
        margin: 0 auto;
        color: white;
        padding: 25px;
        border: 1px solid black;
        border-radius: 50%;
        @media all and (min-width: $size-md) {
            padding: 33px;
        }

        &:before {
            content: '';
            position: absolute;
            top: 3px;
            left: 3px;
            width: calc(100% - 6px);
            height: calc(100% - 6px);
            background-color: black;
            border-radius: 50%;
        }
    }

    &__icon {
        position: relative;
        display: block;
        width: 32px;
        height: 32px;
        @media all and (min-width: $size-md) {
            width: 48px;
            height: 48px;
        }
    }

    &__title {
        margin-top: 8px;
        font-weight: bold;
    }

    @for $i from 1 through length($product-name) {
        &--#{nth($product-name, $i)} {

            &:hover {
                color: nth($product-color, $i);
            }

            .product__figure {
                border-color: nth($product-color, $i);

                &:before {
                    background-color: nth($product-color, $i);
                }
            }
        }
    }
}