.nav {
    list-style: none;
    margin-top: 8px;

    &__item {
        display: block;
    }

    &__link {
        display: flex;
        align-items: center;
        padding: 8px 16px;
        color: $color-text-secondary;
        cursor: pointer;

        .has-subnav > &:after {
            content: ''; 
            width: 0; 
            height: 0;
            margin-left: 4px;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            border-top-width: 5px;
            border-top-style: solid;
        }
    }

    &__icon {
        width: 24px;
        height: 24px;
    }

    &__label {
        flex: 1;
        min-width: 1px;
        margin-left: 16px;
    }

    &--inverted {

        .nav__link {
            color: rgba(white, .8);
        }

        .nav__link:hover,
        .nav__link--active {
            color: white;
            background-color: rgba(white, .2);
        }
    }

    &--inline {
        display: flex;
        align-items: center;
        margin-top: 0;

        .nav__link {
            padding: 10px;
        }
    }

    &__item--user {

        .nav__link {
            padding: 12px 16px;
        }

        .nav__icon {
            width: 36px;
            height: 36px;
        }
    }
}