.menu {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    pointer-events: none;
    z-index: 100;

    @media all and (max-width: $size-xl - 1px) {

        &:after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(black, .4);
            opacity: 0;
            will-change: opacity;
            pointer-events: none;
            transition: opacity .3s cubic-bezier(0,0,0.3,1);
        }

        &--visible {
            pointer-events: auto;

            &:after {
                opacity: 1;
                pointer-events: auto;
            }
        }
    }

    @media all and (min-width: $size-xl) {
        position: static;
        width: $width-sidebar;
        height: auto;
        height: 100vh;
    }
}

.appmenu {
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    width: 90%;
    height: 100%;
    max-width: $width-sidebar;
    background-color: $color-primary;
    color: #fff;
    box-shadow: 0 2px 5px rgba(black, .3);
    transform: translateX(-103%);
    will-change: transform;
    z-index: 101;
    pointer-events: auto;

    @media all and (max-width: $size-xl - 1px) {

        .menu--visible & {
            transform: none;
        }

        .menu--animatable & {
            transition: all .13s ease-in;
        }

        .menu--visible.menu--animatable  & {
            transition: all .33s ease-out;
        }
    }

    @media all and (min-width: $size-xl) {
        width: 100%;
        transform: none;
        box-shadow: none;
    }
}

.open-menu.nav__link {

    @media all and (min-width: $size-xl) {
        display: none;
    }
}