:root {
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -webkit-font-feature-settings: 'lnum';
    -webkit-font-variant-ligatures: no-common-ligatures;
    font-variant-ligatures: no-common-ligatures;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: 'lnum';
}

html {
    font-size: 62.5%;
}

body {
    font-family: $font-primary;
    font-size: 16px;
    font-size: 1.6rem;
    line-height: 1.4;
    color: $color-text-primary;
    text-align: left;
}

h1,
h2,
h3,
h4 {
    font-size: inherit;
    line-height: 1;
    font-weight: inherit;
    line-height: inherit;
    color: inherit;
}

a {
    color: $color-primary;
    cursor: pointer;

    &:hover {
        color: lighten($color-primary, 8%);
        text-decoration: none;
    }
}

.section-title {
    font-size: 22px;
    font-size: 2.2rem;
    line-height: 1;
    @media all and (min-width: $size-md) {
        font-size: 26px;
        font-size: 2.6rem;
    }
}

.content-title {
    font-size: 18px;
    font-size: 1.8rem;
    line-height: 1.4;
    @media all and (min-width: $size-md) {
        font-size: 22px;
        font-size: 2.2rem;
        line-height: 1.3;
    }
}

.content-subtitle {
    font-size: 18px;
    font-size: 1.8rem;
    line-height: 1.4;

    > svg,
    > img {
        position: relative;
        top: 2px;
        width: 20px;
        height: 20px;
    }

    @media all and (min-width: $size-sm) {
        font-size: 18px;
        font-size: 1.8rem;
    }
    @media all and (min-width: $size-xl) {
        font-size: 20px;
        font-size: 2rem;

        > svg {
            width: 24px;
            height: 24px;
        }
    }
}

.content-meta {
    font-size: 13px;
    font-size: 1.3rem;
    line-height: 1.2;
    font-weight: 400;
    @media all and (min-width: $size-lg) {
        font-size: 14px;
        font-size: 1.4rem;
    }
}

.content-container {

    ul:not([class]),
    ol:not([class]) {
        margin-left: 24px;
    }

    * + h2,
    * + h3,
    * + p:not([class]),
    * + ul,
    * + ol {
        margin-top: 1em;
    }

    h2 + p:not([class]),
    h3 + p:not([class]) {
        margin-top: .5em;
    }

    div:empty,
    p:empty {
       display: none;
       margin: 0 !important;
    }
}