.restrict-width {
    max-width: $width-container;    
    
    &.has-padding {
        padding: 16px;

        @media all and (min-width: $size-md) {
            padding: 24px;
        }
    }
}

.centered {
    margin-left: auto;
    margin-right: auto;
}

// Position 
.push-left {
    margin-right: auto;
}

.push-right {
    margin-left: auto;
}

// Typo
.text-center {
    text-align: center;
}

.text-caps {
    text-transform: uppercase;
}

// Responsive visibility
.is-hidden {
    display: none;
}

.is-block {
    display: block;
}

.visible-md {
    @media all and (max-width: $size-md - 1px) {
        display: none !important;
    }
}

.hidden-md {
    @media all and (min-width: $size-md) {
        display: none !important;
    }
}

.visible-lg {
    @media all and (max-width: $size-lg - 1px) {
        display: none !important;
    }
}

.hidden-lg {
    @media all and (min-width: $size-lg) {
        display: none !important;
    }
}