.breadcrumb {
    list-style: none;
    overflow: hidden;
    margin: 0 0 24px; 
    color: $color-text-secondary;

    &__item {
        display: inline;
    }

    &__item::after {
        content: "\0002F";
        padding-right: .5rem;
        padding-left: .5rem;
    }

    &__item:last-child::after {
        display: none;
    }

    &__item--active {

        a {
            color: $color-text-secondary;
        }
    }
}