// Fonts
$font-primary: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Open Sans', 'Helvetica Neue', sans-serif;

// Sizes
$width-container: 1100px;
$width-sidebar: 260px;

$size-sm: 480px;
$size-md: 720px;
$size-lg: 960px;
$size-xl: 1200px;

// Colors
$color-primary: #0099e7;
$color-primary-darker: #127dcf;
$color-secondary: #fd5370;
$color-accent: #ffeb3b;

$product-name: section1 section2 section3 section4 section5 section6 section7 section8;
$product-color: #ac549c #e65e66 #f6ba55 #1cc6d3 #c7d663 #e65f8d #34b2ad #5583a4;

$color-background: #f3f3f3;
$color-divider: #ccc;

$color-text-primary: #6b6b6b;
$color-text-secondary: #96a3ad;
$color-text-hint: #c0c8ce;