html,
body {
    width: 100%;
    width: 100vw;
    min-height: 100%;
    min-height: 100vh;
}

body {
    background-color: $color-background;
}

.app {
    height: 100%;
    height: 100vh;

    @media all and (min-width: $size-xl) {
        display: flex;
        align-items: stretch;
    }
}

.page {
    flex: 1;
}